import React from 'react';

const Topbar = () => (
  <ul className="decklistSidebar__topbar">
    <li className="decklistSidebar__card--name">Name</li>
    <li className="decklistSidebar__card--cost">Cost</li>
  </ul>
);

export default Topbar;


